.scrolling-wrapper-flexbox {
  padding: 20px;

  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  &::-webkit-scrollbar {
    display: none;
  }
}
.card {
  flex: 0 0 auto;
  margin: 20px;
  max-width: 300px;
  height: auto;
}
.imgAvatar {
  height: 50px;
}

.listX:hover {
  background-color: #d6e3f4; /* "#D6E3F4"*/
  cursor: pointer;
}
.listContainer {
  flex: 1 1;
  height: 48vh;
}

